* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Quicksand", sans-serif;
  text-align: center;
  background-color: #eec51d;
  color: #323042;
  font-size: 40px;
  font-weight: 300;
  overflow-x: hidden;
}

#logo-section {
  margin: 10vh auto 5vh auto;
}

#session-length,
#break-length {
  margin: 0 10px;
}

i {
  font-size: 20px;
  transition: 0.5s;
  font-weight: 200;
}

#reset {
  font-size: 1.5rem;
  border-bottom: 2px solid rgba(50, 48, 66, 0);
  transition: 500ms;
  -webkit-transition: 500ms;
  -moz-transition: 500ms;
  -ms-transition: 500ms;
  -o-transition: 500ms;
}

#reset:hover {
  cursor: pointer;
  border-bottom: 2px solid rgba(50, 48, 66, 1);
}

#start_stop {
  font-size: 1.5rem;
  
  border-bottom: 2px solid rgba(50, 48, 66, 0);
  transition: 0.5s;
}

#start_stop:hover {
  cursor: pointer;
  border-bottom: 2px solid rgba(50, 48, 66, 1);
}

#pause-button {

  margin: 0 -5px 0 -5px;
}
#pause-button-right {
  margin: 0 -10px 0 0;
}

.up-arrow:hover {
  cursor: pointer;
}
.down-arrow:hover {
  cursor: pointer;
}

#timer-label {
  font-size: 30px;
  margin-right: 10px;
}

#timer-section {
  margin: 0 auto 0 auto;
}
#time-left {
  font-size: 100px;
}

#length-container {
  margin-top: 10vh;
  display: flex;
  justify-content: space-around;
  margin-bottom: 60px;
}

#reset {
  margin-left: 10px;
}

@media (max-width: 500px) {
  #logo-container {
    transform: scale(0.5) translateX(-40vw);
    -webkit-transform: scale(0.5) translateX(-40vw);
    -moz-transform: scale(0.5) translateX(-40vw);
    -ms-transform: scale(0.5) translateX(-40vw);
    -o-transform: scale(0.5) translateX(-40vw);
  }

  #length-container {
    flex-direction: column;
  }
  i {
    font-size: 30px;
  }
  #break-label{
    margin: 2rem auto 0 auto;
  }
}

#logo path:nth-child(2) {
  stroke-dasharray: 291px;
  stroke-dashoffset: 291px;
  animation: pomo-lines 9s ease-out infinite;
}

#logo path:nth-child(3) {
  stroke-dasharray: 243px;
  stroke-dashoffset: 243px;
  animation: pomo-lines 9s ease-out infinite 2s;
}
#logo path:nth-child(4) {
  stroke-dasharray: 361px;
  stroke-dashoffset: 361px;
  animation: pomo-lines 9s ease-out infinite 3s;
}
#logo path:nth-child(5) {
  stroke-dasharray: 243px;
  stroke-dashoffset: 243px;
  animation: pomo-lines 9s ease-out infinite 4s;
}
#logo path:nth-child(6) {
  stroke-dasharray: 294px;
  stroke-dashoffset: 294px;
  animation: pomo-lines 9s ease-out infinite 5s;
}
#logo path:nth-child(7) {
  stroke-dasharray: 243px;
  stroke-dashoffset: 243px;
  animation: pomo-lines 9s ease-out infinite 6s;
}
#logo path:nth-child(8) {
  stroke-dasharray: 210px;
  stroke-dashoffset: 210px;
  animation: pomo-lines 9s ease-out infinite 7s;
}
#logo path:nth-child(9) {
  stroke-dasharray: 243px;
  stroke-dashoffset: 243px;
  animation: pomo-lines 9s ease-out infinite 8s;
}

@keyframes pomo-lines {
  to {
    stroke-dashoffset: 0;
  }
}

#logo-still path:nth-child(2) {
  animation: pomo-still 0.4s ease forwards;
}

#logo-still path:nth-child(3) {
  animation: pomo-still 0.7s ease forwards;
}
#logo-still path:nth-child(4) {
  animation: pomo-still 0.8s ease forwards;
}
#logo-still path:nth-child(5) {
  animation: pomo-still 1.3s ease forwards;
}
#logo-still path:nth-child(6) {
  animation: pomo-still 1.7s ease forwards;
}
#logo-still path:nth-child(7) {
  animation: pomo-still 2s ease forwards;
}
#logo-still path:nth-child(8) {
  animation: pomo-still 2.3s ease forwards;
}
#logo-still path:nth-child(9) {
  animation: pomo-still 2.5s ease forwards;
}

@keyframes pomo-still {
  from {
    filter: opacity(0);
  }

  to {
    filter: opacity(1);
  }
}
